// Landing Case Study ------------------------------------

.Landing_CaseStudies {
    width: 100%;
    margin: 130px 0;
    margin-bottom: 60px;
}

    .Landing_CaseStudies-Wrap {
        width: 100%;
        position: relative;
    }
        //Project
        .Landing_CaseStudy-Project {
            width: 100%;
            height: 44vw;
            max-height: 700px;
            margin-bottom: 160px;
        }

            .Landing_CaseStudy-ProjectWrap {
                width: 100%;
                height: 100%;
                position: relative;
            }

                .Landing_CaseStudy-Project-Cover {
                    position: absolute;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: #C45527;
                    transition: left .8s ease-in;
                    z-index: 5;
                }


                .Landing_CaseStudy-Card {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    width: 100%;
                    height: calc(100% - 15%);
                    border-radius: 2px;
                    z-index: 1;
                }
                        .CaseStudy-Card1 {
                            background-color: #363535;
                        }
                        .CaseStudy-Card2 {
                            background-color: #444444;
                        }
                        .CaseStudy-Card3 {
                            background-color: rgb(230, 227, 219);
                        }
                        .CaseStudy-Card4 {
                            background-color: #0A0729;
                        }
                        .CaseStudy-Card5 {
                            background-color: #65665B;
                        }

                .Landing_CaseStudy-Text {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    width: 100%;
                    height: calc(100% - 15%);
                    z-index: 2;
                }


                    .Landing_CaseStudy-TextWrap {
                        width: 100%;
                        height: 100%;
                        position: relative;
                        padding: 50px 48px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        color: #FFF;
                    }

                        .Dark-Text {
                        color: #141414;
                    }

                        .Landing_CaseStudy-Image {
                            width: 80px;
                            margin-top: 10px;
                        }

                            .GoDaddy-CardLogo {
                                width: 130px;
                            }
                            .MeetUp-CardLogo {
                                width: 130px;
                            }
                            .Cape-CardLogo {
                                width: 130px;
                            }
                            .Petrolicious-CardLogo {
                                width: 165px;
                            }

                        .Landing_CaseStudy-Description {
                            font-size: 42px;
                            font-weight: 400;
                            width: 50%;
                            max-width: 650px;
                            transition: font-size .4s ease-in;
                        }

                        .Landing_CaseStudy-Button {
                            width: 150px;
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }

                            .Landing_CaseStudy-Button-Text {
                                font-weight: 500;
                                font-size: 18px;
                            }

                        .Landing_CaseStudy-ProductNote {
                            position: absolute;
                            font-size: 18px;
                            top: 40px;
                            left: calc(100% - 48px);
                            width: 300px;
                            white-space: nowrap;
                            transform-origin: top left;
                            transform: rotate(90deg);
                        }

                .Landing_CaseStudy-PhoneWrap {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    z-index: 3;
                    cursor: pointer;
                }

                    .Landing_CaseStudy-PhoneImage {
                        width: 450px;
                        position: absolute;
                        right: 90px;
                        top: -20px;
                        transition: all .4s ease-in;
                    }
                        .Shrink-Landing-Phone {
                            width: 400px;
                            top: 0px;
                        }

@media screen and (max-width: 1600px){
    .Landing_CaseStudy-Description {
        font-size: 32px;
    }
    .Landing_CaseStudy-PhoneImage {
        width: 25vw;
